export default {
	data() {
		return {
			contPhone: '',
			contPass: '',
			passType: 'password',
			isPhoneClear: false,
			isPassClear: false
		};
	},
	
	methods:{
		showPass: function(){
			this.passType = this.passType==='password'?'text':'password'
		},
		clearPhone: function(){
			this.contPhone = ''
		},
		clearPass: function(){
			this.contPass =''
		},
		showCloseIcon:function(e){
			
			if (e.target.value) {
				this.isPhoneClear = true
			}
			else {
				this.isPhoneClear = false
			}
		},
		showPassClearIcon: function(e){
			if (e.target.value){
				this.isPassClear = true
			}else{
				this.isPassClear = false
			}
			
		},
		
		formSubmit:function(e){
			let data = e.detail.value;
			console.log(data);
			
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(data.contPhone))){
				uni.showToast({
					title: '请输入正确电话号码',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if(!data.contPass || data.contPass.length<6){
				uni.showToast({
					title: '请输入至少六位密码',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			uni.showLoading({
				title: '加载中。。',
				mask: true
			});
			uni.request({
				url: this.apiServer+'/ul/login',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					phone: data.contPhone,
					password: data.contPass
				},
				success: res => {
					console.log(res.data);
					if (res.data.status=== 0){
						uni.setStorageSync("token",res.data.data.msg);
						uni.navigateTo({
							url: '/pages/qing-f-c/sellDupty/customer-created/customer-created',
							success: res => {},
							fail: () => {},
							complete: () => {}
						});
						// uni.switchTab({
						// 	url:"/pages/qing-f-c/index"
						// })
					}
					if (res.data.status === 1){
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 3000
						});
					}
				},
				fail: (err) => {
					console.log(err.data)
				},
				complete: () => {
					uni.hideLoading();
				}
			});
			// options: {
			// 	url: this.apiServer+'/ul/login',
			// 	method: 'POST',
			// 	data: {
			// 		phone: data.contPhone,
			// 		password: data.contPass
			// 	}
			// };
			// this.http(this.options).then(res =>{
			// 	console.log(res)
			// }).catch(err=> {
			// 	console.log(err)
			// });
		}
		
	}
}
